<template>
    <div class="cart-content">
      <div class="product-wrapper" v-for="(item, index) in productInfo" :key="index">
        <div class="select" :class="{'isSelect': item.change}" @click="selectChange(item)">
          <img src="./icon/select.png" v-show="item.change">
        </div>
        <div class="product">
          <div class="product-pic">
            <img :src="item.pic" alt="">
          </div>
          <div class="product-info">
            <h4 class="product-info-titile">{{item.name}}</h4>
            <div class="product-info-price">
              <span>¥{{item.price}}</span>
              <span class="product-info-price-model">{{item.model}}</span>
            </div>
            <div class="product-info-number">
              <span class="product-info-number-reduce" @click="numberReduce(item)">-</span>
              <span>{{item.number}}</span>
              <span class="product-info-number-add" @click="numberAdd(item)">+</span>
              <div class="product-info-number-delete" @click="productDelete(index)">
                <img src="./icon/delete.png">
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="cart-footer">
        <div class="select-all">
          <p class="select-all-radio" :class="{'active':allSelected}" @click="activeChange">
            <img src="./icon/select.png" v-show="allSelected">
          </p>
          <p class="select-all-text">全选</p>
          <div class="price-together">
            合计
            <span class="price-together-number">¥{{priceAll}}</span>
          </div>
        </div>
        <div class="button">去结算({{numberAll}})</div>
      </div>
    </div>
</template>

<script>
  import {productInfo} from '@/api/cart.js';
  export default {
    name: 'CartContent',
    data() {
      return {
        isSelect: false,
        productInfo: []
      };
    },
    computed: {
      allSelected () {
        let allSelected = true;
        this.productInfo.map((val) => {
          if (!val.change) {
            allSelected = false;
          }
        });
        if (this.productInfo.length === 0) allSelected = false;
        return allSelected;
      },
      priceAll () {
        let priceAll = 0;
        this.productInfo.map(val => {
          if (val.change) {
            priceAll += val.price * val.number;
          }
        });
        return priceAll;
      },
      numberAll () {
        let numberAll = 0;
        this.productInfo.map(val => {
          if (val.change) {
            numberAll += val.number;
          }
        });
        return numberAll;
      },
      price () {
        return this.productInfo.price;
      },
      number () {
        return this.productInfo.number;
      }
    },
    methods: {
      getProductInfo () {
        return productInfo().then(data => {
          this.productInfo = data.list;
          console.log(data.list);
        });
      },
      activeChange () {
        let isSelect = !this.allSelected;
        this.productInfo.map((v) => {
          v.change = isSelect;
        });
      },
      selectChange (item) {
        item.change = !item.change;
      },
      numberReduce (item) {
        if (item.number > 1) {
          item.number--;
        }
      },
      numberAdd (item) {
        item.number++;
      },
      productDelete (index) {
        console.log(this.productInfo);
        this.productInfo.splice(index, 1);
      }
    },
    created() {
      this.getProductInfo();
    }
  };
</script>
<style  lang="scss" scoped>
@import "~@/assets/scss/mixins";
  .cart-content{
    width: 100%;
    background-color: #fff;
    margin-top: 10px;
    .product-wrapper{
      width: 100%;
      padding: 10px 0;
      height: 120px;
      position: relative;
      border-bottom: 1px solid $border-color;
      .select{
        position: absolute;
        width: 20px;
        height: 20px;
        border: 1px solid $border-color;
        border-radius: 50%;
        top: 40px;
        left: 10px;
      }
      .active,.isSelect{
        border: none;
        img{
          width: 100%;
        }
      }
      .product{
        margin-left: 40px;
        position: relative;
        height: 100%;
        &-pic{
          position: absolute;
          left:0;
          width: 100px;
          height: 100%;
          img{
            width: 100%;
          }
        }
        &-info{
          margin-left: 110px;
          &-titile{
            line-height: 16px;
            color: #000;
            height: 40px;
            font-size: $font-size-l
          }
          &-price{
            color: $header-bgc-translucent;
            font-size: $font-size-l;
            height: 30px;
            padding-top: 5px;
            &-model{
              display: inline-block;
              width: 100px;
              color: #5d656b;
              position: absolute;
              right: 10px;
              text-overflow: ellipsis;
              overflow: hidden;
              white-space: nowrap;
            }
          }
          &-number{
            height: 25px;
            width: 100%;
            font-size: 16px;
            &-reduce,
            &-add{
              display: inline-block;
              border: 1px solid $border-color;
              width: 25px;
              text-align: center;
              height: 25px;
              line-height: 20px;
            }
            &-reduce{
              margin-right: 10px;
            }
            &-add{
              margin-left: 10px;
            }
            &-delete{
              display: inline-block;
              width: 20px;
              float: right;
              margin-right: 10px;
              img{
                width: 100%;
              }
            }
          }
        }
      }
    }
  }
  .cart-footer{
  position: absolute;
  bottom: $tabbar-height;
  width: 100%;
  @include flex-center();
  height: 50px;
  border-bottom: none;
  background-color: #fff;
  z-index: $tabbar-z-index + 1;
  .select-all{
    flex:2;
    &-radio{
      display: inline-block;
      position: absolute;
      left: 10px;
      width: 15px;
      height: 15px;
      border-radius: 50%;
      border: 1px solid $border-color;
    }
    .active{
      border: none;
      img{
        width: 100%;
      }
    }
    &-text{
      margin-left: 30px;
      display: inline-block;
    }
    .price-together{
      display: inline-block;
      padding-left: 30px;
      &-number{
        color: $header-bgc-translucent;
        font-size: $font-size-l;
      }
    }
  }
  .button{
    flex: 1;
    background-color: $header-bgc-translucent;
    color: #fff;
    height: 100%;
    line-height: 50px;
    text-align: center;
    font-size: $font-size-l;
  }
}
</style>



