import axios from 'axios';
export const productInfo = () => {
  return axios.get('static/cart.json').then(res => {
    console.log(res);
    if (res.data) {
      return res.data;
    }
    throw new Error('没有成功获取到数据');
  }).catch(err => {
    if (err) {
      console.log(err);
    }
  });
};



